
          @import "@/assets/themes/AppAddict/variables";
          @import "@/assets/core";
        

.support {
    padding: 15px;
    max-width: 1400px;
    //margin: 0 15px;

    &__title {
        font-size: 22px;
        font-weight: 600;
        margin-bottom: 20px;
        text-align: left;
    }

    &__actions {
        margin-bottom: 30px;
        display: flex;
        justify-content: flex-start;
    }

    &__content {
        min-height: 200px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__loading {
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 200px;
        width: 100%;
    }

    &__empty {
        color: var(--text-secondary);
        font-size: 16px;
        text-align: center;
    }

    &__table-container {
        width: 100%;
        overflow-x: auto;
        background-color: var(--bg-primary);
        border-radius: 8px;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
        border: 1px solid var(--border-color);
    }

    &__table {
        width: 100%;
        border-collapse: collapse;
        table-layout: fixed;

        th,
        td {
            padding: 10px 12px;
            text-align: left;
            border-bottom: 1px solid var(--border-color);
            border-right: 1px solid var(--border-color-light, rgba(0, 0, 0, 0.05));
        }

        th:nth-child(1),
        td:nth-child(1) {
            width: 80px;
        }

        th:nth-child(3),
        td:nth-child(3) {
            width: 100px;
            text-align: center;
        }

        th:last-child,
        td:last-child {
            border-right: none;
        }

        th {
            font-weight: 600;
            color: var(--text-secondary);
            background-color: var(--bg-secondary);

            &:first-child {
                border-top-left-radius: 8px;
            }

            &:last-child {
                border-top-right-radius: 8px;
            }
        }

        tr:last-child td {
            border-bottom: none;
        }
    }

    &__row {
        cursor: pointer;
        transition: background-color 0.2s;

        &:hover {
            background-color: var(--bg-secondary, rgba(0, 0, 0, 0.03));
        }
    }

    &__topic {
        word-break: break-word;
        font-weight: 500;
    }

    &__status {
        display: inline-block;
        padding: 4px 8px;
        border-radius: 4px;
        font-size: 12px;
        font-weight: 600;
        min-width: 70px;
        text-align: center;

        /* Status styling - using the same style as JIT badge */
        background-color: var(--lighter-primary);
        color: var(--primary);
        border: 1px solid rgba(0, 0, 0, 0.05);

        &--closed {
            background-color: var(--bg-success);
            color: black;
        }
    }

    &__load-more {
        display: flex;
        justify-content: center;
        padding: 20px;
        border-top: 1px solid var(--border-color);
        background-color: var(--bg-secondary, #f8f9fa);
    }

    &__loading-more {
        display: flex;
        justify-content: center;
        padding: 20px;
        border-top: 1px solid var(--border-color);
        background-color: var(--bg-secondary, #f8f9fa);
    }
}

.create-ticket-modal {
    padding: 15px;

    &__title {
        margin-bottom: 15px;
        font-size: 18px;
        font-weight: 600;
    }

    &__form {
        margin-bottom: 15px;
    }

    &__field {
        margin-bottom: 12px;

        label {
            display: block;
            margin-bottom: 4px;
            font-weight: 500;
        }
    }

    &__notice {
        margin-top: 10px;
        padding: 8px 12px;
        background-color: #E9EEF9;
        border-radius: 6px;
        font-size: 13px;
        color: #333;
        border-left: 3px solid #2B7CFE;
    }

    &__buttons {
        display: flex;
        justify-content: flex-end;
        gap: 8px;
    }
}
